
<template>
    <div>
        <el-tag type="warning"  style="margin-left: 20px;margin-bottom: 10px;width: 200px;height: 40px;text-align: center;line-height: 40px;font-size: 20px">订单详情</el-tag>
        <el-col :span="1.5" class="el-col" style="float: right;">
            <p class="input-title">&nbsp;</p>
            <el-button type="primary" v-if="orderstatus==1"   @click = "cancelorder">取消订单</el-button>
        </el-col>
        <el-col :span="23" style=" padding-top: 30px;">
            <table class="content-table">
                <tr>
                    <th colspan="8" style="text-align: left;padding-left: 10px">订单信息</th>
                </tr>
                <tr >
                    <td class="title">订单号</td>
                    <td>{{detailData.serialId}}</td>
                    <td class="title">订单来源</td>
                    <td>{{detailData.platIdText}}</td>
                    <td class="title">创建时间</td>
                    <td>{{detailData.crtTime}}</td>
                    <td class="title">支付时间</td>
                    <td>{{detailData.payTime}}</td>
                </tr>
                <tr >
                    <td class="title">出发站</td>
                    <td>{{detailData.depStation}}</td>
                    <td class="title">出发/到达时间</td>
                    <td>{{detailData.depTime}}/{{detailData.destTime}}</td>
                    <td class="title">客户应付</td>
                    <td>{{detailData.amount}}</td>
                    <td class="title">客户实付</td>
                    <td><span v-if="detailData.status!=1&&detailData.status!=7">{{detailData.payAmount}}</span><span v-else>0</span></td>
                </tr>
                <tr >
                    <td class="title">到达站</td>
                    <td>{{detailData.destStation}}</td>
                    <td class="title">手机号</td>
                    <td>{{detailData.contactPhone}}</td>
                    <td class="title">联系人</td>
                    <td>{{detailData.contactName}}</td>
                    <td class="title">订单状态</td>
                    <td>{{detailData.statusText}}</td>
                </tr>

            </table>
            <div style="height: 200px">
                <div style="float: left;margin-top: 60px;width: 30%;position: relative;" v-for="(item,index) in detailData.lines">
                    <p style="position: absolute;width: 100%;text-align: center;top:-10px">{{item.runTime}}</p>
                    <p style="position: absolute;width: 100%;text-align: center;top:20px">{{item.vehicleTypeText}}</p>
                    <el-steps :active="2" align-center style="width: 100%" >
                        <el-step :title="item.depStation" :description="item.depTime"></el-step>
                        <el-step :title="item.destStation" :description="item.destTime"></el-step>
                    </el-steps>
                </div>


            </div>

            <!--标签页-->
            <template >
                <el-tabs type="border-card" @tab-click="handleClick" >
                    <el-tab-pane v-for="(item,index) in detailData.lines" :label="'行程'+(index+1)+' '+(item.depStation)+'--'+(item.destStation)" >

                        <table class="content-table">
                            <tr>
                                <th colspan="6" style="text-align: left;padding-left: 10px">订单信息</th>
                            </tr>
                            <tr >
                                <td class="title">子订单号</td>
                                <td>{{item.subSerialId}}</td>
                                <td class="title">供应商单号</td>
                                <td>{{item.supSerialId}}</td>
                                <td class="title">供应商</td>
                                <td>{{item.supplierName}}</td>
                            </tr>
                            <tr >
                                <td class="title">订单状态</td>
                                <td>{{item.statusText}}</td>
                                <td class="title">出票时间</td>
                                <td>{{item.ticketTime}}</td>
                                <td class="title">出发日期</td>
                                <td>{{item.depDay}}</td>
                            </tr>
                        </table>
                        <table class="content-table">
                            <tr>
                                <th colspan="6" style="text-align: left;padding-left: 10px">车次信息</th>
                            </tr>
                            <tr >
                                <td class="title">班次号</td>
                                <td>{{item.shiftNum}}</td>
                                <td class="title">出发站</td>
                                <td>{{item.depStation}}</td>
                                <td class="title">到达站</td>
                                <td>{{item.destStation}}</td>
                            </tr>
                            <tr >
                                <td class="title">出发/到达时间</td>
                                <td>{{item.depTime}}/{{item.destTime}}</td>
                                <td class="title">距发车时间</td>
                                <td>{{item.distDepTime}}</td>
                                <td class="title">票价</td>
                                <td>{{item.fare}}</td>
                            </tr>
                        </table>
                        <table class="content-table">
                            <tr>
                                <th colspan="11" style="text-align: left;padding-left: 10px">乘客信息</th>
                            </tr>
                            <tr>
                                <td>姓名</td>
                                <td>乘客类型</td>
                                <td>证件类型</td>
                                <td>证件号</td>
                                <td>座位类别</td>
                                <td>退款金额</td>
                                <td>退票手续费</td>
                                <td>验票时间</td>
                                <td>出票状态</td>
                                <td>退票状态</td>
                                <td>操作</td>

                            </tr>
                            <tr v-for="(person,index) in item.passengers">
                                <td>{{person.name}}</td>
                                <td>{{person.passengerTypeText}}</td>
                                <td>{{person.certiTypeText}}</td>
                                <td>{{person.certiNum}}</td>
                                <td>{{person.seatTypeText}}</td>
                                <td>{{person.refundFee}}</td>
                                <td>{{person.serviceFee}}</td>
                                <td>{{person.validateTime}}</td>
                                <td>{{person.ticketStatusText}}</td>
                                <td>{{person.refundStatusText}}</td>
                                <td>
                                    <span class="dothis" v-if="person.ticketStatus==2&&person.refundStatus!=1&&person.passengerType!=4" @click="returnTicket(person.id,item.subSerialId)"> 退票</span>
                                </td>
                            </tr>
                        </table>

                    </el-tab-pane>



                </el-tabs>
                <table class="content-table" style="margin-top: 30px">
                    <tr>
                        <th colspan="6" style="text-align: left;padding-left: 10px">核算明细</th>
                    </tr>
                    <tr>
                        <td>客人应付(单位:元)</td>
                        <td>客人实付(单位:元)</td>
                        <td>支付方式</td>
                        <td>支付流水号</td>
                        <td>支付交易号</td>
                    </tr>
                    <tr >
                        <td>{{detailData.amount}}</td>
                        <td><span v-if="detailData.status!=1&&detailData.status!=7">{{detailData.payAmount}}</span><span v-else>0</span></td>
                        <td>{{detailData.platIdText}}</td>
                        <td>{{detailData.paySerialId}}</td>
                        <td>{{detailData.tradeSerialId}}</td>
                    </tr>
                </table>
            </template>
        </el-col>

    </div>
</template>
<script>
    import ajax from "../model/sys/index"
    export default {
        data() {
            return {
                tag:"行程1",
                activeName2: 'first',
                currentPage: 1,//默认页码
                pageSize:10,//每页显示条数
                totalNum:10,//总条数，
                remainingRefundCoinCount:[],//添加硬币的终端，
                coinNum:"",//添加硬币的个数
                cityOptions: [],
                serverOptions:[],
                checkList:[],
                dialogFormVisible:false,
                why:"",
                formLabelWidth: '120px',
                cityValue: '',
                endNum:"",
                serverStatus:"",
                activateStatus:"",
                detailData: "",
                aaData:[],
                serial:"",
                refundForm:{
                    coins:0,
                    refundOneYuanBack:0,
                    reason:"",
                    serial:""
                },
                orderstatus:"",
                supplierId:""
            };
        },
        methods: {
            handleClick(tab, event) {
                console.log(tab, event);
            },

            getOrderdel:function () {
                ajax.getOrderDetail({serialId:this.serial,supplierId:this.supplierId},res=>{
                    if(res){

                        this.detailData = res.data.result
//                        var station=[]
//                        for(var i=0;i<res.data.result.lines.length;i++){
//                            station.push(res.data.result.lines[i].depStation,res.data.result.lines[i].destStation)
//                        }
                        this.orderstatus = res.data.result.status

                    }

                })
            },
            getCoinOrderLog:function () {
                ajax.getCoinOrderLog({serial:this.serial},res=>{
                    if(res){
                        console.log( res.data)
                        this.logData = res.data.logs

                    }
                })
            },
            cancelorder(){
                this.$confirm('是否继续取消订单操作?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {

                    ajax.orderCancel({serialId:this.serial},res=>{
                        if(res){
                            this.getOrderdel()
                            this.$message({
                                showClose: true,
                                message: '操作成功',
                                type: 'success'
                            });
                        }
                    })
                }).catch(() => {

                });
            },
            returnTicket(id,subSerial){
                var refundData = {
                    passengerId:id,
                    subSerialId:subSerial
                }
                ajax.getRefundInfo(refundData,res=>{

                    this.$confirm('退款金额为：'+res.data.result.refundFee, '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {

                        ajax.refund(refundData,resp=>{
                            if(resp){
                                this.getOrderdel()
                                this.$message({
                                    showClose: true,
                                    message: '操作成功',
                                    type: 'success'
                                });
                            }
                        })
                    }).catch(() => {

                    });
                })


            },
            addCoin:function (data) {
                this.dialogFormVisible = true
                this.addCoinCode = data
                this.coinNum = ""
            },
            orderrefund:function () {
                this.dialogFormVisible = true
                this.refundForm={
                    coins:0,
                    refundOneYuanBack:0,
                    reason:"",
                    serial:""
                },
                ajax.getRebateData({serial:this.serial},res=>{
                    if(res){
                        this.remainingRefundCoinCount=[]

                        this.allowRefundOneYuanBack = res.data.allowRefundOneYuanBack
                        for(var i=1;i<=res.data.remainingRefundCoinCount;i++){
                            this.remainingRefundCoinCount.push(i)
                        }
                    }
                })
            },

            refundSure:function () {
                console.log("000")
                this.refundForm.serial = this.serial
                if(this.refundForm.refundOneYuanBack==true){
                    this.refundForm.refundOneYuanBack=1
                }else {
                    this.refundForm.refundOneYuanBack=0
                }
                if(this.refundForm.reason==""){
                    this.$message({
                        showClose: true,
                        message: '请填写退款原因',
                    });
                    return
                }

                 ajax.goRebate(this.refundForm,res=>{
                    if(res.data.result==0){
                        this.dialogFormVisible = false
                        this.getOrderdel()
                        this.getCoinOrderLog()
                        this.$message({
                            showClose: true,
                            message: '退款成功',
                            type: 'success'
                        });

                    }else {
                        this.$message({
                            showClose: true,
                            message: res.data.message,
                            type: 'fail'
                        });
                    }
                })
            },

            handleSizeChange(val) {
                this.pageSize = val
                this.getCoincount()
            },
            handleCurrentChange(val) {
                this.currentPage = val
                this.getCoincount()
            }
        },
        mounted(){
            this.serial = this.$route.query.serial
            this.supplierId = this.$route.query.supplierId
            this.getOrderdel()

        }
    }
</script>
<style>
    .input-title{
        font-size: 14px !important;
        font-weight: 600;
    }
    .el-col{
        margin-left: 20px;
    }
    ::-webkit-scrollbar-thumb{
        background-color: gainsboro;
    }
    .cell{
        text-align: center;
    }
    td{
        min-width: 50px;
    }
    .title{
        background: #99ccff;
        width: 160px;
    }
    #content-main{
        overflow: scroll;
    }
    .el-table{
        overflow: scroll;
    }
    .content-table{
        width: 100%;
        border-collapse:collapse;
        background-color: white;
        text-align: center;
    }
    .content-table th{
        height: 40px;
        border: 1px solid #F2F3F4;
        text-align: center;
        background-color: gainsboro;
    }
    .content-table td{
        height: 40px;
        border: 1px solid #F2F3F4;
    }
    .dothis{
        color: #3C9CFC;
        cursor:default;
        margin-left: 10px;
    }
    .page-f{
        margin-top: 20px;
        float: right;
    }
    .content-table span:hover{
        color: silver;
    }
    .el-col-23{
        width: 99% !important;
    }
</style>



